<template>
	<div class="page2_2_2">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title1="真实审计" title="协同验证疑点" id="more2_2_2" ydname="审计疑点线索挖掘"/>
			</div>
		</div>
		<div>
			<div class="zb_box box-border-shadow">
				<div v-if="loading" style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
					<i class="am-icon-spinner am-icon-spin"></i> loading
				</div>
				<ul v-else>
					<li class="item" v-for="item in progressData">
						<el-progress color="#c24b4b" type="circle" :percentage="item.value"></el-progress>
						<h3>{{item.name}}</h3>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import reqData from "../../../reqData.js"
	export default{
		name:'page2_2_2',
		data(){
			return{
				msg:["与计划目标比较","真实审计","协同验证"],
				progressData:[],
				loading:true
			}
		},
		components:{
			Place,
			interBtn
		},
		computed:{
			companyID(){
				return JSON.parse(this.$store.getters.getCompany).id;
			},
		},
		methods:{
			getProgressData(){
				reqData
			        .req({
			            apiName: "realShenji",
			            data: {             
			               biaoshi:2,
			               ID:this.companyID
			            }
			        })
			        .then(res => {
			        	res = res.data;
			        	if (res && res.code === "200") {
			          		let data = res.data;
			          		console.log(data);
			          		this.loading=false;
			          		data.forEach(item=>{
			          			item.value=Number(item.value)
			          		})
			          		this.progressData=data;
			       		}
			      	})
			      	.catch(err => {
			       	 	console.log(err);
			      	});
			          		
			}
		},
		mounted(){
			this.getProgressData()
		}
	}
</script>
<style type="text/css" scoped="">
	.zb_box{
		min-height: 600px;
    	text-align: center; 
    	margin: 20px auto;
        width: 100%;
        padding: 30px 60px;
        box-sizing: border-box;
        text-align: justify;
        border-radius: 8px;
        background-color: #fff;
	}
	.zb_box ul{
		overflow: hidden;
	}
	.zb_box li{
		float: left;
		text-align: center;
		width: 16%;
    	margin: 27px 0;
	}
	.zb_box li h3{
		text-align: center;
	}
</style>


